.sidebar-option {
    display: flex;
    align-items: center;
    justify-content: start;
    width: fit-content;
    border-radius: 40px;
    cursor: pointer;
    padding-right: 2rem;
    padding-left: 1rem;
    margin: 0.4rem 0;
    h2 {
        font-size: 1rem;
        font-weight: 400;
        margin-left: 1.2rem;
    }
}

.sidebar-option:hover {
    background-color: var(--color-secondary);
    transition: background-color 0.3s;
}

.sidebar-option_active {
    h2 {
        font-weight: 600;
    }
}

.sidebar-option_icon {
    font-size: 1.8rem !important; 
}